import { Injectable } from '@angular/core';
import { IManagedObject, InventoryService, QueriesUtil } from '@c8y/client';
import {
  ActionControl,
  BulkActionControl,
  Column,
  GridConfig,
  HeaderActionControl,
  AlertService,
  ModalService,
  Status,
  gettext,
  Pagination,
  Row
} from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { assign, forEach, get, identity, transform } from 'lodash-es';
import { AlarmsDeviceGridColumn } from './columns/alarms.device-grid-column';
import { ColumnUtilService } from './columns/column-util.service';
import { GroupDeviceGridColumn } from './columns/group.device-grid-column';
import { ImeiDeviceGridColumn } from './columns/imei.device-grid-column';
import { ModelDeviceGridColumn } from './columns/model.device-grid-column';
import { NameDeviceGridColumn } from './columns/name.device-grid-column';
import { RegistrationDateDeviceGridColumn } from './columns/registration-date.device-grid-column';
import { SerialNumberDeviceGridColumn } from './columns/serial-number.device-grid-column';
import { StatusDeviceGridColumn } from './columns/status.device-grid-column';
import { SystemIdDeviceGridColumn } from './columns/system-id.device-grid-column';
import { DeviceGridActionType, DeviceGridColumn } from './device-grid.models';

@Injectable({
  providedIn: 'root'
})
export class DeviceGridService {
  protected queriesUtil: QueriesUtil;
  protected GRID_CONFIG_DEFAULT_STORAGE_KEY = 'device-grid-config';
  protected DEFAULT_PAGE_SIZE = 20;

  constructor(
    protected inventoryService: InventoryService,
    protected translateService: TranslateService,
    protected alertService: AlertService,
    protected modal: ModalService,
    protected columnUtilService: ColumnUtilService = new ColumnUtilService(translateService)
  ) {
    this.queriesUtil = new QueriesUtil();
  }

  getDefaultColumns(): DeviceGridColumn[] {
    const defaultColumns = [
      new StatusDeviceGridColumn(),
      new NameDeviceGridColumn(),
      new ModelDeviceGridColumn(),
      new SerialNumberDeviceGridColumn(),
      new GroupDeviceGridColumn(),
      new RegistrationDateDeviceGridColumn(),
      new SystemIdDeviceGridColumn(),
      new ImeiDeviceGridColumn(),
      new AlarmsDeviceGridColumn()
    ];

    return defaultColumns;
  }

  getDefaultPagination(): Pagination {
    return {
      pageSize: 10,
      currentPage: 1
    };
  }

  getInfiniteScrollPagination(): Pagination {
    return {
      pageSize: 50,
      currentPage: 1
    };
  }

  getDefaultActionControls(): ActionControl[] {
    return [
      {
        type: DeviceGridActionType.Delete,
        callback: (item: Row) => this.delete(item as IManagedObject)
      }
    ];
  }

  getDefaultBulkActionControls(): BulkActionControl[] {
    return [];
  }

  getDefaultHeaderActionControls(): HeaderActionControl[] {
    return [];
  }

  getProperName(device: IManagedObject): string {
    return this.columnUtilService.getProperName(device);
  }

  getModel(device: IManagedObject): string {
    return this.columnUtilService.getModel(device);
  }

  getSerialNumber(device: IManagedObject): string {
    return this.columnUtilService.getSerialNumber(device);
  }

  getParentsNames(device: IManagedObject, featuredParentId?: string | number): string {
    return this.columnUtilService.getParentsNames(device, featuredParentId);
  }

  getHref(groupOrDevice: IManagedObject, prefix = '#/'): string {
    return this.columnUtilService.getHref(groupOrDevice, prefix);
  }

  getAlarmsHref(device: IManagedObject): string {
    return this.columnUtilService.getAlarmsHref(device);
  }

  getUserConfiguredColumns(columns: Column[]) {
    const config = this.getConfig();
    if (config.columns.length > 0) {
      const reOrderedColumns = [];
      let noConfigColumns = [];
      try {
        noConfigColumns = columns.filter(col => !config.columns.includes(col));
        config.columns.forEach(({ visible, name, sortOrder }) => {
          const columnToReorder = columns.find(col => col.name === name);
          if (columnToReorder) {
            columnToReorder.visible = visible;
            columnToReorder.sortOrder = sortOrder;
            reOrderedColumns.push(columnToReorder);
          }
        });
      } catch (ex) {
        this.clearConfig();
      }
      return [...reOrderedColumns, ...noConfigColumns];
    }
    return columns;
  }

  async delete(device: IManagedObject) {
    try {
      await this.modal.confirm(
        gettext('Delete device'),
        this.translateService.instant(
          gettext(`You are about to delete device: "{{ name }}". Do you want to proceed?`),
          { name: device.name }
        ),
        Status.DANGER,
        { ok: gettext('Delete'), cancel: gettext('Cancel') }
      );
      await this.inventoryService.delete(device);
      this.alertService.success(gettext('Device deleted.'));
    } catch (ex) {
      // only if not cancel from modal

      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  async getData(
    columns: DeviceGridColumn[],
    pagination: Pagination,
    query: any = {},
    withChildren: boolean = false
  ) {
    const filters = {
      ...this.getDevicesFilters(columns, pagination, query),
      withParents: true,
      withChildren
    };
    return this.inventoryService.list(filters);
  }

  async getChildDeviceData(
    columns: DeviceGridColumn[],
    pagination: Pagination,
    query: any = {},
    withChildren: boolean = false,
    id: string
  ) {
    const childDeviceFilters = true;
    const filters = {
      ...this.getDevicesFilters(columns, pagination, query, childDeviceFilters),
      withParents: true,
      withChildren
    };
    return this.inventoryService.childDevicesList(id, filters);
  }

  async getCount(columns: DeviceGridColumn[], pagination: Pagination, query: any = {}) {
    const filters = {
      ...this.getDevicesFilters(columns, pagination, query),
      pageSize: 1,
      currentPage: 1
    };
    return (await this.inventoryService.list(filters)).paging.totalPages;
  }

  async getCountChildDevices(
    columns: DeviceGridColumn[],
    pagination: Pagination,
    query: any = {},
    id: string
  ) {
    const childDeviceFilters = true;
    const filters = {
      ...this.getDevicesFilters(columns, pagination, query, childDeviceFilters),
      pageSize: 1,
      currentPage: 1
    };
    return (await this.inventoryService.childDevicesList(id, filters)).paging.totalPages;
  }

  async getTotalChildDevices(query: any = {}, id: string): Promise<number> {
    const filters = {
      q: this.queriesUtil.buildQuery(query),
      pageSize: 1,
      withTotalPages: true
    };
    return (await this.inventoryService.childDevicesList(id, filters)).paging.totalPages;
  }

  async getTotal(query: any = {}): Promise<number> {
    const filters = {
      q: this.queriesUtil.buildQuery(query),
      pageSize: 1,
      withTotalPages: true
    };
    return (await this.inventoryService.list(filters)).paging.totalPages;
  }

  getDeviceQueryString(columns: DeviceGridColumn[], query: any): string {
    let fullQuery = this.getQueryObj(columns);
    fullQuery = this.queriesUtil.addAndFilter(fullQuery, query);
    return this.queriesUtil.buildQuery(fullQuery);
  }

  getQueryObj(columns: DeviceGridColumn[], defaultFilter = {}): any {
    return transform(columns, (query, column) => this.extendQueryByColumn(query, column), {
      __filter: {},
      __orderby: [],
      ...defaultFilter
    });
  }

  getConfig(key: string = this.GRID_CONFIG_DEFAULT_STORAGE_KEY): GridConfig {
    const config = JSON.parse(localStorage.getItem(key));
    if (config === null) {
      return { columns: [], pagination: { pageSize: this.DEFAULT_PAGE_SIZE, currentPage: 1 } };
    }
    return config as GridConfig;
  }

  saveConfig(config: GridConfig, key: string = this.GRID_CONFIG_DEFAULT_STORAGE_KEY) {
    localStorage.setItem(key, JSON.stringify(config));
  }

  clearConfig(key: string = this.GRID_CONFIG_DEFAULT_STORAGE_KEY) {
    localStorage.removeItem(key);
  }

  private getHardware(device: IManagedObject): any {
    const hardwarePropertyName = this.isVendme(device)
      ? 'com_nsn_startups_vendme_fragments_VendingMachineTypeInfo'
      : 'c8y_Hardware';
    return device && device[hardwarePropertyName];
  }

  private isVendme(device: IManagedObject) {
    return device.type === 'com_nsn_startups_vendme_VendingMachine';
  }

  private getDevicesFilters(
    columns: DeviceGridColumn[],
    pagination: Pagination,
    query: any,
    childDeviceFilters?: boolean
  ) {
    return {
      ...(childDeviceFilters
        ? { query: this.getDeviceQueryString(columns, query) }
        : { q: this.getDeviceQueryString(columns, query) }),
      pageSize: pagination.pageSize,
      currentPage: pagination.currentPage,
      withChildren: false,
      withTotalPages: true
    };
  }

  private extendQueryByColumn(query: any, column: DeviceGridColumn): void {
    if (column.filterable && column.externalFilterQuery) {
      const getFilter = column.filteringConfig.getFilter || identity;
      const queryObj = getFilter(column.externalFilterQuery);

      if (queryObj.__or) {
        query.__filter.__and = query.__filter.__and || [];
        query.__filter.__and.push(queryObj);
      } else if (queryObj.__and && get(query, '__filter.__and')) {
        queryObj.__and.map(obj => query.__filter.__and.push(obj));
      } else {
        assign(query.__filter, queryObj);
      }
    }

    if (column.sortable && column.sortOrder) {
      const cs = {};
      forEach(column.sortingConfig.pathSortingConfigs, pathSortingConfig => {
        cs[pathSortingConfig.path] =
          (column.sortOrder === 'asc' ? 1 : -1) * (pathSortingConfig.sortOrderModifier || 1);
      });
      query.__orderby.push(cs);
    }
    return query;
  }
}
