import { Component, forwardRef, OnInit, ViewChild, HostListener, ElementRef } from '@angular/core';
import { get, cloneDeep } from 'lodash-es';
import { FilteringFormRendererContext } from '@c8y/ngx-components';
import { SchemaFormComponent } from '../schema-form.component';
import { DeviceGridColumn } from '../device-grid.models';

@Component({
  templateUrl: './base.filtering-form-renderer.component.html'
})
export class BaseFilteringFormRendererComponent implements OnInit {
  @ViewChild(forwardRef(() => SchemaFormComponent), { static: false })
  schemaFormComponent: any;
  schema;
  form;
  model;
  options;

  constructor(public context: FilteringFormRendererContext) {}

  @HostListener('keydown.enter', ['$event']) onEnterKeyDown(event: KeyboardEvent) {
    event.stopPropagation();
    this.applyFilter();
  }
  @HostListener('keydown.escape', ['$event']) onEscapeKeyDown(event: KeyboardEvent) {
    event.stopPropagation();
    this.resetFilter();
  }

  ngOnInit() {
    const column: DeviceGridColumn = this.context.property;
    this.schema = cloneDeep(column.filteringConfig.schema);
    this.form = cloneDeep(column.filteringConfig.form);
    this.model = cloneDeep(column.externalFilterQuery || {});
    this.options = { formDefaults: { ngModelOptions: { debounce: 100 } } };
  }

  ngAfterViewInit() {
    setTimeout(() => {
      try {
        this.schemaFormComponent.elementRef.nativeElement.querySelector('input').focus();
      } catch (e) {
        // intended empty
      }
    }, 10);
  }

  canApply() {
    return get(this.schemaFormComponent, '$componentScope.vm.ngForm.$invalid');
  }

  applyFilter() {
    this.context.applyFilter({
      externalFilterQuery: this.model
    });
  }

  resetFilter() {
    this.context.resetFilter();
  }
}
